import React from "react";
import "./Education.css";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";

import { Pagination } from "swiper";
import "swiper/css/pagination";
import profilePic1 from "../../img/icseLogo.jpg";
import profilePic2 from "../../img/collegeLogo.png";
const Testimonial = () => {
  const clients = [
    {
      img: profilePic1,
      review:
        "Senior Secondary School Certificate (ISC) from PlayWay English School,Fatehpur Uttar Pradesh, India. Year:2018-2019",
        
    },
    {
      img: profilePic2,
      review:
        "Bachelor of Technology (B.Tech) in Computer Science from GLA University, Mathura, Uttar Pradesh, India. Year:2020-2024.",
    },
    
  ];

  return (
    <div className="t-wrapper" id="testimonial">
      <div className="t-heading">
        <span>My</span>
        <span> Education</span>
      <div className="blur t-blur1" style={{ background: "var(--purple)" }}></div>
      <div className="blur t-blur2" style={{ background: "skyblue" }}></div>

      </div>
      <Swiper
        // install Swiper modules
        modules={[Pagination]}
        slidesPerView={1}
        pagination={{ clickable: true }}
      >
        {clients.map((client, index) => {
          return (
            <SwiperSlide key={index}>
              <div className="testimonial">
                <img src={client.img} alt="" />
                <span>{client.review}</span>
              </div>
            </SwiperSlide>
          );
        })}
      </Swiper>
    </div>
  );
};

export default Testimonial;
